.home-page {
  margin-top: 20px;
  margin-left: 20px;
  margin-right: 20px;
  // min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
background-color: cornflowerblue;

h2{
  font-size: 1.8em;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 20px;
  padding-top: 20px;
  letter-spacing: .25em;
  line-height: 1.65;
}
h3{
  font-size: 1em;
  font-weight: 500;
  margin-left: 20px;
  letter-spacing: .25em;
  line-height: 1.5;
}

p{
  font-size: 1em;
  font-weight: 400;
  margin-left: 20px;
  margin-right: 20px;
  padding-top: 20px;
}
a{
  color: rgb(173, 32, 32);
}

  }

  @media screen and (max-width: 600px) {
    .home-page  {
      margin: 0
    }
  }

  @media screen and (max-width: 1279px) {
    .home-page  {
    
     margin-left: '20px';
     margin-top: '20px';
    }
